#root {
  height: 100%;
}

html {
  background-color: white;
}

body {
  height: 100%;
  overflow-x: hidden;
  margin-left: 75px;
  margin-right: 75px;
}

header {
  height: 100%;
  width: 100%;
}

main {
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

