.App-header {
  background-color: white;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #6ec5f7;
  font-size: 20px;
}

.links-div {
  margin: auto;
  margin-right: 10px;
}

.links-div-link {
  margin-left: 20px;
  margin-right: 20px;
}

.slogan {
  font-size: 40px;
  font-weight: bold;
  color: #303030;
  margin: 20px;
  margin-top: 0px;
}

.home-img {
  width: 65vw;
  max-width: 575px;
}

.symbol {
  padding: 0px;
  height: 32px;
  margin-top: 4px;
  margin-right: 10px;
  border-style: solid;
  border-width: 1.5px;
  border-radius: 5px;
  border-color: lightgrey;
}

.symbol:hover {
  border-color: lightgrey;
  background-color: #efefef;
  filter: brightness(85%) sepia(89%) hue-rotate(-7deg);
}

.profile-img {
  max-width: 200px;
  max-height: 200px;
  border-radius: 10%;
}

.profile-img-head {
  margin-top: 0px;
  max-height: 34px;
  vertical-align: middle;
  border-radius: 5px;
}

.profile-div {
  margin-top: 0px;
  display: flex;
  align-items: center;
  text-align: center;
  margin-right: 0px;
  margin-bottom: 0px;
  padding: 0px;
}

.grey {
  background-color: #efefef;
  padding: 1px;
  padding-bottom: 1.5px;
  border-radius: 5px;
}

.grey:hover {
  background-color: rgb(254, 240, 186);
}

.profile-top-right-name {
  color: #4d4d4d;
  text-decoration: none;
  float: right;
  margin: 5px;
  margin-top: 6px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 16px;
  text-align: left;
  vertical-align: middle;
}

.chat-list {
  width: 22vw;
  height: calc(100vh - 65px);
  overflow-y: auto;
  overflow-x: visible;
  scrollbar-width: none;
  border-style: solid;
  border-color: lightgrey;
  border-width: 0px 1px 0px 0px;
  padding-left: 10px;
}

.chat-list::-webkit-scrollbar {
  display: none;
}

.chat-list-title {
  font-size: 25px;
  font-weight: bold;
  margin: 0px;
  margin-bottom: 10px;
}

.chat-list-item {
  height: 70px;
  display: flex;
  align-items: center;
  padding-left: 10px;
  margin-right: 10px;
  margin-left: 0px;
}

.chat-list-item-active {
  height: 70px;
  display: flex;
  align-items: center;
  background-color: #eeeeee;
  padding-left: 10px;
  margin-right: 10px;
  margin-left: 0px;
  border-radius: 10px;
}

.chat-list-item-text {
  text-align: left;
  margin: auto;
  margin-left: 12px;
  margin-bottom: 0px;
  max-width: 75%;
}

.chat-list-item-profile {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.chat-list-item-name {
  font-size: 16px;
  color: black;
  margin: 0px;
  padding: 0px;
}

.chat-list-item-name-unread {
  font-size: 16px;
  color: black;
  font-weight: bold;
  margin: 0px;
  padding: 0px;
}

.chat-list-item-message {
  font-size: 14px;
  color: #999999;
  margin-top: 0px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-bottom: 1px;
}

.chat-list-item-message-unread {
  font-size: 14px;
  color: black;
  font-weight: bold;
  margin-top: 0px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-bottom: 1px;
}

.chat-focus {
  display: flex;
  flex-direction: column;
  flex-grow: 100;
}

.chat-focus-top {
  display: flex;
  padding: 10px;
  align-items: center;
  border-style: solid;
  border-color: lightgrey;
  border-width: 0px 0px 1px 0px;
  margin-bottom: 10px;
}

.chat-focus-top-text {
  text-align: left;
  margin-left: 12px;
  margin-bottom: 0px;
  align-items: center;
}

.chat-focus-top-name {
  font-size: 18px;
  font-weight: bold;
  margin: 0px;

}

.chat-focus-top-desc {
  font-size: 14px;
  color: #999999;
  margin: 0px;
}

.chat-focus-top-profile {
  align-items: center;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.chat-focus-messages::-webkit-scrollbar {
  display: none;
  min-height: calc(100vh - 200px);
  max-height: calc(100vh - 200px);
}

.chat-focus-messages {
  overflow: scroll;
  scrollbar-width: none;  /* Firefox */
  min-height: calc(100vh - 200px);
  max-height: calc(100vh - 200px);
}

.chat-focus-bottom {
  width: 100%;
}

.chat-focus-bottom-left-outer {
  text-align: left;
}

.chat-focus-bottom-right-outer {
  text-align: right;
}

.chat-focus-bottom-left {
  display: inline-block;
  text-align: left;
  background-color: rgb(254, 240, 186);
  margin: 1px;
  margin-left: 10px;
  border-radius: 10px;
}

.chat-focus-bottom-right {
  display: inline-block;
  text-align: right;
  background-color: #0e9ef1;
  margin: 1px;
  margin-right: 10px;
  border-radius: 10px;
}

.chat-focus-bottom-message-left {
  margin: 0px;
  padding: 7px 12px 7px 12px;;
  font-size: 14px;
}

.chat-focus-bottom-message-right {
  margin: 0px;
  padding: 7px 12px 7px 12px;;
  font-size: 14px;
  color: white;
}

.chat-focus-type {
  width: 97.5%;
  text-align: center;
  vertical-align: bottom;
  align-self: center;
  padding-right: 8px;
  padding-top: 10px;
}

.chat-focus-type-input {
  width: 100%;
  font-size: 14px;
  border-radius: 10px;
  text-indent: 8px;
  padding: 8px;
  padding-right: 0px;
  border-style: solid;
  border-color: darkgrey;
  border-width: 0px;
  background-color: #f2f2f2;
  color: black;
}

.chat-focus-type-input::placeholder {
  color: grey;
  opacity: 0.8; /* Firefox */
}


